/** Represents a datalayer error */
export class DataLayerError {
  public errorType: DataLayerErrorType;
  public correlationId: string;
  public statusCode: number;

  constructor(errorType: DataLayerErrorType, correlationId?: string, statusCode?: number) {
    this.errorType = errorType;
    this.correlationId = correlationId;
    this.statusCode = statusCode;
  }

  public toString(): string {
    return `ErrorType: ${this.errorType}, StatusCode: ${this.statusCode}, CorrelationId: ${this.correlationId}`;
  }
}

/** Represents the type of datalayer error */
export enum DataLayerErrorType {

  /** XMLHttpRequest.onError transport level failure */
  TransportError,

  /** HTTP status code not in the 200 -> 300 range */
  ResponseError,

  /** Error parsing the response */
  ParseError,

  /** Timeout */
  TimeOutError,

  /** Unknown error. This should never happen. Means unhandled error */
  UnknownError,
}
